<script setup lang="ts">
import useEventCommerce from "~/composables/events/use-event-commerce";
import { GAME_TYPE } from "~/constants";

export interface CardLiveProps {
  title?: string;
  geniusId?: string;
  sportType?: TSportType;
  gameType?: TGameType;
  commerceCoverage: TCommerceCategory[];
  team1Name?: string;
  team2Name?: string;
  team1Color?: string;
  team2Color?: string;
  team1LogoUrl?: string;
  team2LogoUrl?: string;
  team1Score?: number;
  team2Score?: number;
  isEventAvailableForUser?: boolean;
  statisticTooltipTitle?: string;
  sportTypeTooltipTitle?: string;
  gameTime?: string;
}

const props = defineProps<CardLiveProps>();
const { commerceType } = useEventCommerce(props.commerceCoverage);

const sportType = computed(() => (props.sportType || "other").toLowerCase());
const bannerImageUrl = computed(() => `/images/banners/live/${sportType.value}.png`);
const isPracticeGameType = computed(() => props.gameType === GAME_TYPE.TRAINING || Boolean(props.team1Name && !props.team2Name));
const isOtherGameType = computed(() => props.gameType === GAME_TYPE.OTHER || Boolean(!props.team1Name && !props.team2Name));
const bannerImageFallbackUrl = "/images/banners/live/other.png";
const hasStatistics = computed(() => Boolean(props.geniusId));

const hasValidScores = computed(() =>
  props.team1Score !== undefined && props.team2Score !== undefined,
);

const isTeam1Winning = computed(() =>
  hasValidScores.value && props.team1Score! > props.team2Score!,
);

const isTeam2Winning = computed(() =>
  hasValidScores.value && props.team2Score! > props.team1Score!,
);

const showGameTime = computed(() => Boolean(props.gameTime));

const gridLayoutClasses = computed(() => {
  if (showGameTime.value) {
    return "grid grid-cols-7 sm:grid-cols-8 lg:grid-cols-10 xl:grid-cols-12 gap-1";
  }
  else {
    return "grid grid-cols-3 gap-2";
  }
});

const team1ColSpan = computed(() => {
  if (!showGameTime.value) {
    return "";
  }
  return "col-span-2 sm:col-span-2 lg:col-span-3 xl:col-span-4";
});

const team2ColSpan = computed(() => {
  if (!showGameTime.value) {
    return "";
  }
  return "col-span-2 sm:col-span-3 lg:col-span-4 xl:col-span-4";
});

const scoreColSpan = computed(() => {
  if (!showGameTime.value) {
    return "";
  }
  return "col-span-2 sm:col-span-2 lg:col-span-2 xl:col-span-3";
});
</script>

<template>
  <div v-motion-pop class="cardLive">
    <div class="relative">
      <div class="cardLive__bannerImageTitleWrap">
        <div class="cardLive__bannerImageTitle">
          <b style="background-color: var(--base-error-color); padding: 2px 8px; border-radius: 2px">Live</b>
          Watch now
        </div>
      </div>
      <BaseImage
        class="cardLive__bannerImage"
        :src="bannerImageUrl"
        :fallback-src="bannerImageFallbackUrl"
        alt="Live banner"
        width="full"
        :height="80"
      />
    </div>

    <div class="cardLive__content min-h-[102px]">
      <div :class="gridLayoutClasses">
        <div
          v-if="showGameTime"
          class="w-10 h-5 flex items-center justify-center text-[10px] text-secondary font-bold bg-neutral-light-200 dark:bg-neutral-dark-900 rounded-lg py-0.5 px-2 self-start"
        >
          <span>{{ props.gameTime }}</span>
        </div>

        <div v-if="props.team1Name" :class="`flex justify-center items-center ${team1ColSpan}`">
          <BaseLogo
            :src="props.team1LogoUrl"
            :icon-background="props.team1Color"
            with-hover
            no-background
            :title="team1Name"
            with-initials
            fit="contain"
          />
        </div>
        <div v-else :class="`flex justify-center items-center ${team1ColSpan}`" />

        <div v-if="!(isPracticeGameType || isOtherGameType)" :class="`flex items-center justify-center ${scoreColSpan}`">
          <div v-if="hasValidScores" class="cardLive__scoreContainer">
            <div class="cardLive__scoreNumber">
              {{ props.team1Score }}
            </div>
            <div v-if="isTeam1Winning" class="cardLive__winningArrow rtl:scale-x-[-1] transform-gpu">
              ◀
            </div>
            <div class="cardLive__scoreDivider">
              -
            </div>
            <div v-if="isTeam2Winning" class="cardLive__winningArrow rtl:scale-x-[-1] transform-gpu">
              ▶
            </div>
            <div class="cardLive__scoreNumber">
              {{ props.team2Score }}
            </div>
          </div>
          <div v-else class="text-xs font-bold text-secondary">
            VS
          </div>
        </div>
        <div v-else :class="`flex justify-center items-center ${scoreColSpan}`" />

        <div v-if="props.team2Name" :class="`flex justify-center items-center ${team2ColSpan}`">
          <BaseLogo
            :src="props.team2LogoUrl"
            :icon-background="props.team2Color"
            with-hover
            no-background
            :title="team2Name"
            with-initials
            fit="contain"
          />
        </div>
        <div v-else :class="`flex justify-center items-center ${team2ColSpan}`" />
      </div>

      <div :class="`${gridLayoutClasses} items-center mt-2`">
        <div v-if="showGameTime" class="w-10" />

        <div v-if="props.team1Name" :class="`flex justify-center ${team1ColSpan}`">
          <h3 class="cardLive__teamTitle text-caption-medium text-main">
            {{ props.team1Name }}
          </h3>
        </div>
        <div v-else :class="`flex justify-center ${team1ColSpan}`" />

        <div :class="`flex justify-center ${scoreColSpan}`" />

        <div v-if="props.team2Name" :class="`flex justify-center ${team2ColSpan}`">
          <h3 class="cardLive__teamTitle text-caption-medium text-main">
            {{ props.team2Name }}
          </h3>
        </div>
        <div v-else :class="`flex justify-center ${team2ColSpan}`" />
      </div>
      <div v-if="isOtherGameType" class="h-[60px] flex items-center justify-center">
        <p class="line-clamp-2 text-main text-base font-medium">
          {{ props.title }}
        </p>
      </div>
    </div>

    <div class="hr" role="separator" />

    <EventMetaInfo
      class="!mt-0 p-3"
      :title="props.title"
      :sport-type="props.sportType"
      :is-other-game-type="isOtherGameType"
      :is-practice-game-type="isPracticeGameType"
      :has-statistics="hasStatistics"
      :team-name="props.team1Name"
    />

    <div v-if="!props.isEventAvailableForUser" class="cardLive__lock text-caption-medium text-secondary">
      <IconLock class="z-10" />

      <span class="ml-2 z-10">{{ commerceType }}</span>
    </div>
  </div>
</template>

