  @apply flex flex-col max-w-full
  overflow-hidden cursor-pointer
  transition duration-100
  hover:shadow-2xl;

  background-color: var(--base-card-bgColor);
  border-radius: var(--base-border-radius);

  .cardLive__content {
    @apply p-3 flex flex-col justify-between flex-1 relative;

    .iconStatistic:hover path {
      fill: var(--base-error-color);
    }
  }

  .cardLive__bannerImageTitleWrap {
    @apply absolute top-0 left-0 w-full h-full flex justify-center items-center;
  }

  .cardLive__bannerImageTitle {
    @apply text-base font-black text-white uppercase italic;
  }

  .cardLive__bannerImage {
    @apply w-full object-cover rounded-t-sm h-20;
  }

  .cardLive__teamTitle {
    @apply max-w-full line-clamp-2 text-center;
  }

  .cardLive__scoreContainer {
    @apply flex items-center justify-center gap-2;
  }

  .cardLive__winningArrow {
    @apply text-xs font-bold animate-pulse;
    color: var(--base-primary-accent-color, var(--base-primary-color));
    line-height: 1;
    font-size: 10px;
  }

  .cardLive__scoreNumber {
    @apply text-lg font-bold;
    color: var(--base-text-color);
  }

  .cardLive__scoreDivider {
    @apply text-base font-medium;
    color: var(--base-secondary-color);
  }

  .cardLive__lock {
    @apply px-2 py-2 overflow-hidden
    flex justify-center items-center;

    background-color: var(--base-card-bgSecondaryColor);
  }

  .hr {
    background: var(--base-background-color);
  }
}
</style>
