<script setup lang="ts">
import useEventCommerce from "~/composables/events/use-event-commerce";
import { GAME_TYPE } from "~/constants";
import formatDate from "~/utils/format-date";

export interface CardUpcomingProps {
  title?: string;
  gameStartDate?: null | number | string | Date;
  sportType?: TSportType;
  commerceCoverage: TCommerceCategory[];
  team1Name?: string;
  team2Name?: string;
  team1Color?: string;
  team2Color?: string;
  team1LogoUrl?: string;
  team2LogoUrl?: string;
  isEventAvailableForUser?: boolean;
  gameType: TGameType;
}

const props = defineProps<CardUpcomingProps>();

const { locale: currentLocale } = useI18n();

const eventDate = computed(() => formatDate(
  props.gameStartDate,
  currentLocale.value === "ko" ? "MMMM do" : "d MMMM",
  currentLocale.value as TAvailableLocale,
));
const dateHours = formatDate(props.gameStartDate, "HH:mm", currentLocale.value as TAvailableLocale);
const isPracticeGameType = computed(() => props.gameType === GAME_TYPE.TRAINING || Boolean(props.team1Name && !props.team2Name));
const isOtherGameType = computed(() => props.gameType === GAME_TYPE.OTHER || Boolean(!props.team1Name && !props.team2Name));
const { commerceType } = useEventCommerce(props.commerceCoverage);
</script>

<template>
  <div v-motion-pop class="cardUpcoming">
    <div class="cardUpcoming__date text-main pt-8 pb-4 px-1">
      <span class="text-xs">{{ eventDate }}</span>
      <span class="text-lg font-bold mb-5">{{ dateHours }}</span>
    </div>

    <div class="cardUpcoming__content">
      <div v-if="!props.isEventAvailableForUser" class="cardUpcoming__lock text-caption-medium">
        <IconLock class="z-10" />

        <span class="z-10">{{ commerceType }}</span>
      </div>
      <div class="flex-1">
        <BlockTeamsInfo
          class="h-14 mt-1"
          :team1-name="props.team1Name"
          :team2-name="props.team2Name"
          :team1-color="props.team1Color"
          :team2-color="props.team2Color"
          :team1-logo-url="props.team1LogoUrl"
          :team2-logo-url="props.team2LogoUrl"
          :title="props.title"
          :game-type="props.gameType"
        />
        <EventMetaInfo
          :title="props.title"
          :team-name="props.team1Name"
          :is-other-game-type="isOtherGameType"
          :is-practice-game-type="isPracticeGameType"
          :sport-type="props.sportType"
        />
      </div>
    </div>
  </div>
</template>

