  @apply flex flex-row max-w-full
  overflow-hidden cursor-pointer
  transition duration-100;

  &:hover {
    box-shadow: var(--base-box-shadow);
  }

  background-color: var(--base-card-bgColor);
  border-radius: var(--base-border-radius);

  .cardUpcoming__date {
    @apply w-20 flex flex-col flex-shrink-0 justify-center items-center text-center;

    background-color: var(--base-card-bgSecondaryColor);
  }

  .cardUpcoming__content {
    @apply relative p-3 pt-6
    max-w-full flex-grow-0
    flex flex-col justify-between;

    width: calc(100% - 64px);
  }

  .cardUpcoming__lock {
    @apply absolute top-0 right-0 px-2 py-1 overflow-hidden flex justify-center items-center gap-[0.5rem]
    rounded-tr rounded-bl rtl:right-auto rtl:left-0 rtl:rounded-tr-none rtl:rounded-bl-none rtl:rounded-br;

    color: var(--base-secondary-color);

    &::before {
      @apply absolute w-full h-full z-0;

      content: '';
      background-color: var(--base-card-bgSecondaryColor);
    }
  }
}
</style>
